.card {
    display:inline-block;
    padding:30px;
    border-radius:5px;
    margin:8px 5px;
    -webkit-transition:.5s;
    -o-transition:.5s;
    transition:.5s;
    border:solid 2px #fff;
}
.card img {
    height:150px;
    padding-bottom:10px;
    cursor:pointer;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}
.card h2, .card p {
    color:#fff;
}


